.App {
  text-align: center;
  .TranslateButton {
    position: fixed;
    border: none;
    background: white;
    border-radius: 40px;
    width: 40px;
    height: 40px;
    display: inline-block;
    align-items: center;
    font-weight: bold;
    font-size: 12px;
    bottom: 20px;
    right: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
    perspective: 1000px;
    animation: continuousFlip 3s infinite linear;
  }
  .TranslateButton.flipping {
    animation: flip 0.6s forwards;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes continuousFlip {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
